// Demo script for Azora MVE (Minimum Viable Ecosystem)
// Tests the full event-driven flow

const demo = async () => {
  console.log('üåæ Azora MVE Demo - Farmer Pest Report Flow\n');

  // Simulate farmer submitting a pest report
  const pestReport = {
    farmId: 'FARM-001',
    pestName: 'Fall Armyworm',
    timestamp: new Date().toISOString()
  };

  console.log('üêõ Farmer submits pest report:', pestReport);

  try {
    // Make the API call
    const response = await fetch('http://localhost:3001/report', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(pestReport),
    });

    if (response.ok) {
      console.log('‚úÖ Pest report processed successfully!');
      console.log('\nüîÑ Simulated Event Flow:');
      console.log('1. üì° Synapse Gateway received report');
      console.log('2. üõ∞Ô∏è Oracle fetched weather data');
      console.log('3. üîç Nexus analyzed pest + weather');
      console.log('4. üìú Covenant stamped recommendation');
      console.log('5. üß¨ Genome logged all events');
      console.log('6. üì± Real-time push sent to farmer app');

      console.log('\nüéØ Expected Result:');
      console.log('- Farmer app shows: "Apply neem oil immediately (High urgency)"');
      console.log('- Based on Fall Armyworm + low rainfall conditions');

      console.log('\nüöÄ Demo complete! Open http://localhost:3001/farmer to see the UI');
    } else {
      console.error('‚ùå Failed to process report');
    }
  } catch (error) {
    console.error('‚ùå Error:', error.message);
  }
};

// Run demo if called directly
if (import.meta.url === `file://${process.argv[1]}`) {
  demo();
}

export { demo };